export function validateDataUsername (username) {
  if (username.length < 4) return ' ';
  if (username.length > 150) return '  ';
  if (/.*[\s].*/.test(username)) return '  .'
  if (!/^[a-zA-Z]/.test(username)) return '    '
  if (!/^[a-zA-Z][a-zA-Z0-9-_@+-]{3,150}$/.test(username)) return '          "@ _ + - "'
  return false
}

export function validateDataPassword (password) {
  if (password.length < 6) return ' .      6 ';
  if (/^\d+$/.test(password)) return '      :     ,      .'
  if (/^[a-zA-Z]+$/.test(password)) return '      :     ,      .'
  if (/.*[--].*/.test(password)) return '   .'
  if (/.*[\s].*/.test(password)) return '  .'
  if (!/^.*[A-Z].*$/.test(password)) return '     ,         '
  if (!/^.*[0-9].*$/.test(password)) return '    ,          '
  if (/^[a-zA-Z0-9]+$/.test(password)) return '     '
  return false
}

export function validateDataEmail (email) {
  if (!/@[^.]+\.[a-z]{2,4}$/.test(email)) return '    '
  return false
}
